.AboutUs-pageheader{
    margin-top: 35px;
    text-align: center;
}
.AboutUs-pageheader h1{
    font-size: 2rem;
    line-height: 1.5rem;
    font-weight: bold;
    color: #454545;
    padding-top: 5px;
}
.AboutUs-pageheader .breadcrumb{
    justify-content: center;
    background-color: transparent;
    margin-bottom: 10px;
    display: flex;
    padding: 0.75rem 1rem;
    list-style: none;
    border-radius: 0.25rem;
}
.AboutUs-pageheader .breadcrumb .breadcrumb-item{
    font-size: 1rem;
    line-height: 1.5rem;
    color: #454545;
    text-transform: capitalize;
}
.AboutUs-pageheader .breadcrumb .breadcrumb-item a{
    color: #454545;
    font-size: 1rem;
}
.AboutUs-pageheader .breadcrumb .breadcrumb-item a:hover{
    color: black;
    text-decoration: none;
}
.AboutUs-pageheader .breadcrumb .active::before{
    content: ">";
}
.AboutUs-content{
    background-image: url("https://halongcomplex.sunworld.vn/wp-content/themes/halongcomplex/dist/images/background.png");
    background-size: cover;
    background-position: center;
    background-color: #f1f5f7;
    background-repeat: no-repeat;
    background-attachment: fixed;
    padding: 40px 0;
}
.AboutUs-content .detail-content{
    background-color: #fff;
    padding: 20px;
    font-size: initial;
}
.AboutUs-content .detail-content img{
    max-width: 100%;
    border-radius: 2%;
}
.AboutUs-content .side-content .widget{
    background-color: #fff;
    padding: 1.5rem;
    font-weight: bold;
    display: block;
}
.AboutUs-content .side-content .widget h3{
    color: #454545;
    margin-bottom: 20px;
    font-weight: bold;
    font-size: 1.5rem;
    line-height: 2.25rem;
}
.AboutUs-content .side-content .widget .list-box{
    margin-left: -15px;
    margin-right: -15px;
}
.AboutUs-content .side-content .widget .list-box .box{
    border-bottom: 0.063rem solid #eceff1;
    margin-bottom: 0.938rem;
    position: relative;
    padding: 0px 15px;
}
.AboutUs-content .side-content .widget .list-box .box .img-box{
    height: 6.875rem;
    overflow: hidden;
    border-radius: 0.313rem;
    position: relative;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.AboutUs-content .side-content .widget .list-box .box .img-box img{
    opacity: 0;
    visibility: hidden;
    width: 100%;
}
.AboutUs-content .side-content .widget .list-box .box .img-box::after{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 3;
    top: 0;
    left: 0;
    border-radius: 5%;
    background-color: #026fb5b3;
    transition: all 0.5s ease;
    opacity: 0;
}
.AboutUs-content .side-content .widget .list-box .box .img-box .aboutus-button-plus{
    z-index: 4;
    position: absolute;
    top: 50%;
    left: 50%;
    color: white;
    transform: translate(-50% , -50%);
    font-size: 40px;
    transition: all 0.7s ease;
    opacity: 0;
}
.AboutUs-content .side-content .widget .list-box .box .img-box:hover::after{
    opacity: 1;
}
.AboutUs-content .side-content .widget .list-box .box .img-box:hover .aboutus-button-plus{
    opacity: 1;
}
.AboutUs-content .side-content .widget .list-box .box a{
    color: black;
}
.AboutUs-content .side-content .widget .list-box .box a:hover{
    text-decoration: none;
}
.AboutUs-content .side-content .widget .info{
    background-color: #026fb5;
    padding: 1rem;
    color: #fff;
    margin-bottom: 1.875rem;
}
.AboutUs-content .side-content .widget .info h2{
    font-weight: bold;
    font-size: 1rem;
    line-height: 1.313rem;
    margin-bottom: 5px;
}